* {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 17px;
  background: #F4F4F4;
  color: #ffffff;
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
}

button,
input {
  font: inherit;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.button {
  width: 147px;
  height: 55px;
  border: 0;
}

.button-primary {
  background-color: #DC0D27;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}
.button-primary:hover {
  background-color: #cc0017;
}

.button-secondary {
  background-color: #F4F4F4;
  color: #000000;
  width: 140px;
  border: 2px solid #000000;
  transition: background-color 0.3s ease-in-out;
}
.button-secondary:hover {
  background-color: #e4e4e4;
}

.input-primary {
  width: 420px;
  height: 55px;
  background-color: #E5E5E5;
  padding-left: 15px;
  border: 0;
  transition: background-color 0.3s ease-in-out;
}
.input-primary:hover {
  background-color: #d5d5d5;
}

.button-importance {
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out;
}

.button-importance.default {
  background-color: #A9BB30;
}
.button-importance.default:hover {
  background-color: #99ab20;
}

.button-importance.so-so {
  background-color: #F3EC34;
}
.button-importance.so-so:hover {
  background-color: #e3dc24;
}

.button-importance.important {
  background-color: #DC0D27;
}
.button-importance.important:hover {
  background-color: #cc0017;
}

.count-number {
  position: absolute;
  content: "";
  left: 10px;
  top: 20px;
  z-index: 1;
}

.header {
  background-color: #ffffff;
  color: #000000;
  box-shadow: 0px 10px 63px rgba(0, 0, 0, 0.07);
}

.header__container {
  display: flex;
  justify-content: center;
  height: 50px;
  align-items: center;
}

.header__logo {
  margin-right: 50px;
}

.header__title {
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}

.main {
  padding: 94px 0 144px 0;
}

.main__container {
  display: flex;
  gap: 50px;
  padding: 0 75px;
  flex-wrap: wrap;
  background: #F4F4F4;
}

.pomodoro-form {
  width: 733px;
  margin: 0 auto;
}

.window__panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px 30px 19px 30px;
  background-color: #000000;
}

.window__body {
  background-color: #E5E5E5;
  margin-bottom: 50px;
  padding: 70px 166px 108px;
}

.window__timer-text {
  color: #333333;
  font-size: 150px;
  line-height: 183px;
  margin-bottom: 36px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.window__buttons {
  display: flex;
  justify-content: center;
  gap: 23px;
}

.task-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-form__add-button {
  width: 230px;
}

.pomodoro-tasks {
  width: 507px;
  margin: 0 auto;
}

.pomodoro-tasks__header-title {
  color: #333333;
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
  margin-bottom: 3px;
}

.pomodoro-tasks__quest-list {
  color: #333333;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 33px;
  margin-bottom: 31px;
  counter-reset: taskNumber;
  margin-bottom: 30px;
}

.pomodoro-tasks__quest-tasks {
  margin-bottom: 20px;
}

.pomodoro-tasks__list-task {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 17px;
  color: #333333;
  border-bottom: 1px solid #E5E5E5;
  padding: 0 15px 0 35px;
}

.pomodoro-tasks__list-task::after {
  content: "";
  width: 25px;
  height: 25px;
  left: 0;
  position: absolute;
  border: 1px solid #000000;
  border-radius: 50%;
  z-index: 0;
}

.pomodoro-tasks__list-task.default::after {
  background-color: #A9BB30;
}

.pomodoro-tasks__list-task.so-so::after {
  background-color: #F3EC34;
}

.pomodoro-tasks__list-task.important::after {
  background-color: #DC0D27;
}

.pomodoro-tasks__list-task:first-child {
  border-top: 1px solid #E5E5E5;
}

.pomodoro-tasks__task-text {
  border: none;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  padding: 4px 5px;
}
.pomodoro-tasks__task-text:hover {
  background-color: rgba(68, 220, 13, 0.15);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.pomodoro-tasks__task-text_active {
  background-color: rgba(220, 13, 39, 0.15);
  transition: background-color 0.3s ease-in-out;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.pomodoro-tasks__task-text_active:hover {
  background-color: rgba(204, 0, 23, 0.3);
}

.pomodoro-tasks__task-button {
  height: 6px;
  width: 26px;
  border: 0;
  background: url("../img/svg/burger-button.svg") no-repeat;
}

.burger-popup__edit-button {
  background-image: url(../img/svg/pen.svg);
  background-repeat: no-repeat;
  background-position: 13px 10px;
}

.burger-popup__delete-button {
  background-image: url(../img/svg/bin.svg);
  background-repeat: no-repeat;
  background-position: 13px 10px;
}

.pomodoro-tasks__deadline-timer {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 17px;
  color: #999999;
}

.modal-overlay {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 3;
}

.modal-delete {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 175px;
  width: 350px;
  padding: 25px 83px;
  background: #ffffff;
}

.modal-delete__title {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 17px;
}

.modal-delete__close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  height: 24px;
  width: 24px;
  background: url(../img/svg/close-btn.svg) no-repeat center;
  border: 0;
}

.modal-delete__delete-button {
  height: 50px;
  width: 162px;
  padding: 19px 50px;
  border: 0;
  margin-bottom: 10px;
}

.modal-delete__cancel-button {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 17px;
  text-decoration: underline;
  border: 0;
  background: transparent;
}

.burger-popup {
  position: absolute;
  display: none;
  flex-direction: column;
  height: 83px;
  width: 166px;
  padding: 5px 1px 0 1px;
  right: 0;
  top: 45px;
  z-index: 1;
  border: 1px solid #C4C4C4;
}

.burger-popup_active {
  display: flex;
}

.burger-popup::before {
  content: "";
  position: absolute;
  top: -5px;
  right: 22px;
  height: 8px;
  width: 8px;
  border: 1px solid #C4C4C4;
  border-right: transparent;
  border-bottom: transparent;
  transform: rotate(45deg);
  background-color: #F4F4F4;
  z-index: 2;
}

.popup-button {
  height: 36px;
  width: 163px;
  padding: 9px 15px 9px 40px;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  border: 0;
  background-color: #F4F4F4;
  text-align: left;
  z-index: 2;
}

.popup-button:hover {
  background-color: #E5E5E5;
}